.slides__clearance {
  width: 350px;
  margin-left: auto;
  margin-right: auto;
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  text-align: center;
  padding: 10px 20px;
  background: linear-gradient(to left, black, darkslategray);
  box-shadow: 0 2px 2px gray;
  text-shadow: 0 0 2px rgb(231, 231, 231);
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  display: flex;
  align-items: center;
}

.slides__clearanceSale {
  font-size: 40px;
  font-weight: 500;
  padding-right: 15px;
  color: white;
}

.slides__icon {
  color: white;
}

.slides {
  margin: 50px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.slides__img {
  width: 480px;
  height: 400px;
  /* object-fit: contain; */
  background-color: black;
  /* border: 3px solid black; */
  box-shadow: 5px 5px 10px rgb(63, 63, 63);
  border-radius: 50%;
}

.slides__right {
  margin-left: 80px;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 55%;
}

.slides__tag {
  color: black;
  font-size: 25px;
  /* margin-bottom: 20px; */
  font-weight: 500;
}

.slides__shoes {
  color: red;
