}

.slides__saleDiv {
  background: linear-gradient(to right, rgb(7, 196, 64), rgb(20, 161, 20));
  max-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 30px;
  border-radius: 20px;
  margin-top: 40px;
