}

.slides__upto {
  font-size: 30px;
  color: rgb(223, 223, 223);
  font-weight: 500;
}

.slides__off {
  font-size: 50px;
  font-weight: 600;
  color: black;
}

.slides__button {
  border: none;
  outline: none;
  color: rgb(224, 224, 224);
  background: linear-gradient(to right bottom, rgb(179, 0, 0), red);
  font-size: 25px;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 7px;
  cursor: pointer;
  transition: all 0.5s ease;
}

.slides__button:hover {
  background: rgb(233, 231, 231);
  color: red;
  border: 1px solid red;
}

