.checkout {
  max-width: 100vw;
  min-height: 100vh;
  background: linear-gradient(to right, rgb(39, 39, 39), rgb(71, 126, 126));
}

.checkout__empty {
  margin-top: 40px;
  background: linear-gradient(to right, pink, rgb(228, 228, 228));
  display: flex;
  flex-direction: column;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  border: 1px solid pink;
  box-shadow: 2px 2px 2px pink;
  border-radius: 40px;
  padding: 20px 0;
}

.checkout__empty1 {
  color: rgb(43, 43, 43);
  font-size: 30px;
  text-decoration: underline;
  margin-bottom: 25px;
  width: 80%;
  text-align: center;
}

.checkout__empty2 {
  font-size: 20px;
  color: rgb(88, 88, 88);
  width: 80%;
  text-align: center;
}

.checkout__notEmpty {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.checkout__notEmpty > span {
  margin-top: 35px;
  margin-bottom: 50px;
  color: rgb(226, 225, 225);
  text-decoration: underline;
  font-size: 30px;
}

@media screen and (max-width: 450px) {
  .checkout__empty {
    margin-top: 25px;
    width: 70%;
    border-radius: 20px;
    padding: 15px 0;
  }
  
  .checkout__empty1 {
    font-size: 20px;
    margin-bottom: 18px;
    width: 80%;
  }
  
  .checkout__empty2 {
    font-size: 16px;
    width: 80%;
  }

  .checkout__notEmpty > span {
    margin-top: 12px;
    margin-bottom: 33px;
    color: rgb(226, 225, 225);
    text-decoration: underline;
    font-size: 20px;
  }
}
.shoeItem {
  margin-bottom: 60px;
  border: 3px solid black;
  box-shadow: 5px 5px 5px black;
  border-radius: 20px;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.shoeItem > img {
  width: 320px;
  height: 300px;
  border-right: 1px solid black;
  width: 40%;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.shoeItem__right {
  display: flex;
  flex-direction: column;
  width: 60%;
  background: linear-gradient(to right, rgb(233, 233, 233), rgb(199, 199, 199));
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.shoeItem__title {
  background: linear-gradient(to right, rgb(123, 191, 214), rgb(245, 198, 198));
  width: 100%;
  font-size: 25px;
  font-weight: 600;
  text-align: center;
  padding: 15px 0;
  color: rgb(58, 57, 57);
  border-top-right-radius: 20px;
  border-bottom: 1px solid black;
}

.shoeItem__category {
  margin-top: 10px;
  font-size: 17px;
  font-weight: 400;
  color: rgb(94, 93, 93);
  text-align: center;
}

.shoeItem__price {
  margin-top: 60px;
  text-align: center;
  color: rgb(0, 87, 0);
  font-size: 25px;
}

.shoeItem__afterPrice {
  letter-spacing: 1px;
  color: rgb(0, 179, 0);
  font-size: 35px;
  font-weight: 500;
}

.shoeItem__remove {
  background: linear-gradient(to right, brown, rgb(223, 102, 102));
  margin-top: 18px;
  width: 250px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  color: rgb(216, 216, 216);
  font-size: 20px;
  font-weight: 500;
  padding: 7px 0;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid brown;
  transition: all 0.5s ease;
}

.shoeItem__click {
  padding-left: 15px;
  padding-right: 7px;
}

.shoeItem__remove:hover {
  background: linear-gradient(to left, rgb(184, 183, 183), white);
  color: black;
}

@media screen and (max-width: 360px) {
  .shoeItem {
    margin-bottom: 40px;
    border-radius: 10px;
    width: 80%;
    height: 400px;
    flex-direction: column;
    align-items: center;
  }

  .shoeItem > img {
    width: 100%;
    height: 220px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0;
  }
  
  .shoeItem__right {
    width: 100%;
    height: 100%;
